import ValidarCpf from './ValidarCpf';

export default class GeraCPF {
    constructor() {
        this.cpfSemDigito = this.rand();
        this.valida = new ValidarCpf();
    }
    rand(min = 100000000, max = 999999999) {
        return String(Math.floor(Math.random() * (max - min) + min))
    }

    formatado(cpf) {
        return (
            cpf.slice(0, 3) + '.' +
            cpf.slice(3, 6) + '.' +
            cpf.slice(6, 9) + '-' +
            cpf.slice(9, 11) 
        );
    }
    geraNovoCpf() {
        const digito1 = this.valida.pegaDigito(this.cpfSemDigito);
        const digito2 = this.valida.pegaDigito(this.cpfSemDigito + digito1);
        
        const novoCpf = this.cpfSemDigito + digito1 + digito2;

        return this.formatado(novoCpf);
    }
}